.App{
  background-color:rgba(255, 193, 7, 0.932);
  min-height:500px;
  width:400px;
  margin : 10px auto;
  margin-top: 80px;
  padding:20px;
  
  
}

body{
  background-color:#4d647cde;
}
.App input{
  padding: 10px;
  margin:10px;
  border:0;
  border-radius:3px;
  width:270px;
  height:30px;
  background-color:#e67e22;
  color:white;
  font-size:15px;
  
}
.App input:focus,button:focus{
  outline: none;
  color:white;
  
}
.App input::placeholder{
  color:white;

}

.App button{
  height:45px;
  width:70px;
  margin:5px;
  border:0;
  border-radius:3px;
  background-color:teal;
  font-size:15px;
  cursor: pointer;
  color:white;

}

